#header {
  position: absolute;
  z-index: 50;
  padding-top: 32px;
  width: 100%; }

.menu {
  display: flex;
  justify-content: flex-end; }
  .menu :nth-child(6) a {
    margin-right: 68px; }
  .menu :nth-last-child a {
    margin-right: 0; }

.menu-li {
  list-style-type: none; }
  .menu-li a {
    line-height: 1;
    text-decoration: none;
    margin-right: 38px;
    color: black; }
    .menu-li a:hover {
      color: #EB5B31; }

.down img {
  margin-top: 8px;
  transition: ease 0.5s; }

.down .submenu {
  position: absolute;
  list-style-type: none;
  padding: 0;
  white-space: nowrap;
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0; }
  .down .submenu li {
    margin-top: 8px; }
  .down .submenu a:hover {
    color: #EB5B31; }

.down:hover img {
  transform: rotate(180deg);
  transition: ease 0.5s; }

.down:hover .submenu {
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
  transition: ease 0.5s;
  z-index: 10; }

